.forthem {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  grid-column: content-start/content-end;
  justify-content: space-around;
  padding: 2.5rem;
}
.forthem-container {
  border: 0.8rem double black;
  border-radius: 0.2rem;
  height: 30rem;
  margin: 1rem;
  -moz-transition: all 0.8s;
  -o-transition: all 0.8s;
  -webkit-transition: all 0.8s;
  transition: 0.8s;
  width: 42rem;
  &:hover {
    cursor: pointer;
    -moz-box-shadow: 0px 6px 15px 0px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 0px 6px 15px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 6px 15px 0px rgba(0, 0, 0, 0.75);
    .floating-text {
      opacity: 1;
      transform: translateY(0);
    }
  }
}
.floating-text {
  color: rgb(0, 0, 0);
  font-size: 4rem;
  font-weight: 400;
  margin: 1rem;
  opacity: 0;
  position: absolute;

  transform: translateY(4rem);
  transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  -webkit-transition: all 0.5s;
}

.forthem-img {
  height: 100%;
  width: 100%;
  z-index: -1;
  position: relative;
  top: 0;
  left: 0;
}

.forthem-text {
  font-size: 3rem;
  text-align: center;
}
