.header {
  align-items: center;
  border-bottom: 1.5rem double;
  display: flex;
  grid-column: page-start/page-end;
  height: 16vh;
  justify-content: space-between;
  padding: 2.5rem;
  .header-left {
    display: flex;
  }
  .header-right {
    display: flex;
  }
}
.logo {
  height: 13vh;
  left: 50%;
  position: absolute;
  transform: translateX(-50%);
}
.header-link {
  -moz-transition: all 0.3s;
  -o-transition: all 0.3s;
  -webkit-transition: all 0.3s;
  color: rgb(0, 0, 0);
  cursor: pointer;
  font-size: 2.4rem;
  font-weight: 500;
  margin: 2rem;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.3s;
  &:hover {
    color: black;
    transform: scale(1.05);
  }
}
.logout {
  -moz-transition: all 0.3s;
  -o-transition: all 0.3s;
  -webkit-transition: all 0.3s;
  color: rgb(0, 0, 0);
  cursor: pointer;
  font-size: 2.4rem;
  font-weight: 500;
  margin: 2rem;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.3s;
  &:hover {
    color: black;
    transform: scale(1.05);
  }
}
.toggle-button {
  display: none;
}
.active-class {
  -moz-transition: all 0.3s;
  -o-transition: all 0.3s;
  -webkit-transition: all 0.3s;
  font-weight: bolder;
  transform: scale(1.05);
  transition: 0.3s;
  &:hover {
    color: black;
    font-weight: bolder;
    transform: scale(1.05);
  }
}
@media (max-width: 1024px) {
  .header {
    justify-content: center;
    .header-left {
      display: none;
    }
    .header-right {
      display: none;
    }
    .toggle-button {
      display: block;
    }
  }
}
@media (max-width: 600px) {
  .header {
    height: 12vh;
  }
  .logo {
    height: 10vh;
  }
}
