.modal {
  background-color: white;
  border: 0.8rem black double;
  border-radius: 0.5rem;
  display: flex;
  height: 80%;
  justify-content: space-around;
  left: 50%;
  position: fixed;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
  z-index: 6;
}
.modal-addedtext {
  color: rgb(37, 110, 35);
  font-size: 2.2rem;
  max-width: 100%;
}
.modal-imgcontainer {
  padding: 5rem;
  position: relative;
  width: 60%;
  p {
    font-size: 2.5rem;
    text-align: center;
  }
}
.modal-img {
  height: 30rem;
  width: 30rem;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}
.modal-info {
  border-left: double rgb(15, 15, 15) 0.4rem;
  display: flex;
  flex-direction: column;
  font-size: 2rem;
  height: 90%;
  padding: 1rem;
  position: relative;
  top: 5%;
  width: 40%;
  p {
    margin: 0.5rem;
  }
}
.modal-close {
  cursor: pointer;
  font-size: 2.6rem;
  position: absolute;
  right: 0.5rem;
  top: 0.5rem;
}
.modal-buttons {
  .close-button {
    background-color: rgb(247, 91, 91);
    border: double 1rem black;
    bottom: 2rem;
    cursor: pointer;
    font-size: 1.8rem;
    padding: 0.8rem;
    position: absolute;
    right: 1rem;
    -moz-transition: all 0.3s;
    -o-transition: all 0.3s;
    -webkit-transition: all 0.3s;
    transition: 0.3s;
    &:hover {
      transform: scale(1.05);
    }
    &:active {
      transform: translateY(0.2rem);
    }
  }
  .add-button {
    border: double 1rem black;
    bottom: 2rem;
    cursor: pointer;
    font-size: 1.8rem;
    padding: 0.8rem;
    position: absolute;
    right: 10rem;
    -moz-transition: all 0.3s;
    -o-transition: all 0.3s;
    -webkit-transition: all 0.3s;
    transition: 0.3s;
    &:hover {
      transform: scale(1.05);
    }
    &:active {
      transform: translateY(0.2rem);
    }
  }
}
@media (max-width: 2000px) {
  .modal {
    height: 75%;
    width: 80%;
  }
}
@media (max-width: 1024px) {
  .modal-img {
    height: 30rem;
    width: 28rem;
  }
}
@media (max-width: 992px) {
  .modal-img {
    height: 25rem;
    width: 25rem;
  }
}

@media (max-width: 650px) {
  .modal {
    align-content: center;
    flex-direction: column;
    height: 70%;
    justify-content: center;
  }
  .modal-imgcontainer {
    height: 45rem;
    width: 100%;
  }
  .modal-info {
    align-self: center;
    border-left: none;
    border-top: 0.5rem double rgb(194, 192, 192);
    width: 80%;
  }
  .modal-img {
    height: 28rem;
    width: 28rem;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}
@media (max-width: 350px) {
  .modal {
    align-content: center;
    flex-direction: column;
    height: 70%;
    justify-content: center;
  }
  .modal-imgcontainer {
    height: 35rem;
    width: 100%;
  }
  .modal-info {
    align-self: center;
    border-left: none;
    border-top: 0.5rem double rgb(194, 192, 192);
    width: 80%;
  }
  .modal-img {
    height: 80%;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 50%;
  }
}
