.forher {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  grid-column: content-start/content-end;
  height: min-content;
  justify-content: space-around;
  padding: 2.5rem;
}
.forher-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: min-content;
  justify-content: space-around;
}
.forher-quote {
  align-content: center;
  display: flex;
  justify-content: center;
  padding: 2rem;
  width: 100%;
}
.forher-quote-img {
  height: 25rem;
  width: 20rem;
}
.forher-quote-text {
  font-size: 2rem;
  padding: 4rem;
  width: 70%;
}
.forher-quote-text-name {
  font-size: 1.8rem;
  font-style: italic;
  margin: 1rem;
  text-align: right;
}
.selects {
  align-content: center;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
}
.select-general {
  -moz-appearance: none;
  background-color: white;
  border: double black 0.6rem;
  border-radius: 0.2rem;
  font-size: 2rem;
  height: 5rem;
  margin-bottom: 3rem;
  margin-top: 2rem;
  outline: none;
  width: 28rem;
}
.fetch-error {
  font-size: 3rem;
  margin: 3rem;
  text-align: center;
  width: 100%;
}
@media (max-width: 600px) {
  .forher {
    margin: 0.5rem;
    padding: 0.5rem;
  }
  .forher-quote-text {
    padding: 2rem;
    width: 100%;
  }
  .select-all {
    align-content: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}
