.forhim {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  height: min-content;
  padding: 2.5rem;

  grid-column: content-start/content-end;
}
.forhim-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  height: min-content;
}
.forhim-quote {
  display: flex;
  align-content: center;
  justify-content: center;
  width: 100%;
  padding: 2rem;
}
.forhim-quote-img {
  height: 25rem;
  width: 20rem;
}
.forhim-quote-text {
  font-size: 2rem;
  width: 70%;
  padding: 4rem;
}
.forhim-quote-text-name {
  font-size: 1.8rem;
  font-style: italic;
  text-align: right;
  margin: 1rem;
}
.selects {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-around;
  align-content: center;
}
.select-general {
  outline: none;
  border-radius: 0.2rem;
  border: double black 0.6rem;
  font-size: 2rem;
  height: 5rem;
  width: 28rem;
  margin-top: 2rem;
  margin-bottom: 3rem;
  -moz-appearance: none;
  background-color: white;
}
.fetch-error {
  margin: 3rem;
  width: 100%;
  font-size: 3rem;
  text-align: center;
}
.no_matches {
  margin: 5rem;
  font-size: 3rem;
}
@media (max-width: 600px) {
  .forhim {
    padding: 0.5rem;
    margin: 0.5rem;
  }
  .forhim-quote-text {
    width: 100%;
    padding: 2rem;
  }
  .select-all {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: center;
  }
}
