.allproducts {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  grid-column: content-start/content-end;
  padding: 2.5rem;
}
.allproducts-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}
.selects {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-around;
  align-content: center;
}
.select-general {
  outline: none;
  border-radius: 0.2rem;
  border: double black 0.6rem;
  font-size: 2rem;
  height: 5rem;
  width: 28rem;
  margin-top: 2rem;
  margin-bottom: 3rem;
  -moz-appearance: none;
  background-color: white;
}
.fetch-error {
  margin: 3rem;
  width: 100%;
  font-size: 3rem;
  text-align: center;
}
@media (max-width: 600px) {
  .allproducts {
    padding: 0.5rem;
    margin: 0.5rem;
  }
  .selects {
    display: flex;
    flex-direction: column;

    justify-content: space-between;
    align-content: center;
  }
}
