.cart {
  display: flex;
  flex-direction: column;
  grid-column: content-start/content-end;
  //justify-content: center;
  align-items: center;
  margin-top: 5rem;
  min-height: 65vh;
  margin-bottom: 5rem;
}
.cart-text {
  border-bottom: double 1.5rem black;
  font-size: 4rem;
  text-align: center;
  width: 100%;
}
.cart-orderbutton {
  align-self: flex-end;
  border: double black 1rem;
  cursor: pointer;
  font-size: 2rem;
  height: 5rem;
  margin-top: 1rem;
  text-align: center;
  transition: 0.3s;
  width: 27rem;
  &:disabled {
    background-color: rgb(150, 150, 150);
    cursor: not-allowed;
  }
  &:active {
    transform: translateY(0.2rem);
  }
  &:hover {
    transform: scale(1.05);
  }
}
.cart-require {
  align-self: flex-end;
  color: red;
}
.cart-total {
  align-self: flex-end;
  font-size: 2rem;
  margin-top: 1rem;
  height: 5rem;
  width: 20rem;
}
