.bestSelling {
  grid-column: content-start/content-end;
  min-height: 45rem;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  flex-direction: row;
  height: min-content;
}
.bestSelling-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 2.5rem;
  flex-direction: row;

  width: 100%;
}
.bestSellingText {
  text-align: center;
  border-bottom: double black 1.5rem;
  height: 20%;
  width: 100%;
  font-size: 4rem;
}
.fetch-error {
  margin: 3rem;
  font-size: 5rem;
  text-align: center;
}
@media (max-width: 600px) {
  .bestSelling-container {
    padding: 1rem;
  }
}
