.homeheader {
  grid-column: page-start/page-end;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  min-height: 45rem;
  width: 100%;
  padding: 2.5rem;
}
.lady-img {
  height: 45rem;
}
.cotton-img {
  position: relative;
  top: 25%;
  height: 25rem;
}
.offer {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  position: relative;
  width: 35rem;
  height: 100%;
}
.offer-text {
  margin-bottom: 1rem;
  font-size: 3.5rem;
  text-align: center;
  text-decoration: none;
}
.offer-button {
  align-self: center;
  height: min-content;
  padding: 1rem;
  border: double 1rem black;
  border-radius: 0.4rem;

  text-align: center;
  text-decoration: none;
  cursor: pointer;
  transition: 0.4s;
  -moz-transition: all 0.4s;
  -o-transition: all 0.4s;
  -webkit-transition: all 0.4s;
  font-weight: 500;
  a {
    text-decoration: none;
    font-size: 2.6rem;
    color: black;
  }
  &:hover {
    -webkit-box-shadow: 0px 3px 15px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 3px 15px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 3px 15px 0px rgba(0, 0, 0, 0.75);
    transform: scale(1.05);
  }
}

@media (max-width: 1024px) {
  .homeheader {
    min-height: 40rem;
    justify-content: space-around;
    margin-bottom: 0;
    overflow: hidden;
  }
  .lady-img {
    top: 5%;
    height: 40rem;
  }
  .cotton-img {
    display: none;
  }
  .homeheader-text {
    top: 10%;
    width: 35rem;
  }
  .homeheader-text-quote {
    font-size: 3rem;
  }
}
@media (max-width: 600px) {
  .homeheader {
    min-height: 35rem;
    width: 100%;
    justify-content: center;
    padding: 1rem;
    padding-left: 0;
    padding-right: 2.5rem;
  }
  .lady-img {
    top: 5%;
    height: 33rem;
  }
  .cotton-img {
    display: none;
  }
  .homeheader-text {
    top: 0;
    width: 20rem;
    font-size: 2.5rem;
    margin-right: 1rem;
  }
  .homeheader-button {
    font-size: 1.5rem;
    width: 22rem;
  }
}
